*{
  padding:0;
  margin:0;
  box-sizing: border-box;
}

.contain{
  width:100vw;
  height: 100vh;
  display: grid;
  place-items:center;
  background:rgb(0, 0, 0);
  position:relative;
  overflow:hidden;
}


.head{
  width:100%;
  height:8%;
  background:rgb(255, 187, 0);
  color:rgb(51, 44, 44);
  text-align:center;
  text-transform:capitalize;
  font-size:4vmin;
  top:0vmin;
  padding-top:1vmin;
  position: absolute;
  font-weight:bold;
}

#title,#textValue{
  position:absolute;
  width:80%;
  height:5%;
  background:rgb(71, 64, 64);
  color:whitesmoke;
  border-radius:4vmin;
  padding:2vmin;
  transition:all 300ms ease-in-out;
  outline:none;border:none;
  font-weight:bold;
  font-size:2vmin;
  transform-origin:left;
}
#title:focus{
  border:.5vmin solid rgb(255, 196, 0);
  transform:scale(1.02)translateX(-5%);
}
#textValue:focus{
  border:.5vmin solid rgb(255, 196, 0);
  transform:scale(1.02)translateX(-5%);
}
#title{
  top:10%;
 
}
#textValue{
  top:15.5%;
}

#add{
  width:5vmin;
  height:5vmin;
  background:yellow;
  color:black;
  font-weight:bold;
  border:none;
  outline:none;
  border-radius:50%;
  font-size: 4vmin;
  position:absolute;
  top:10%;
  right:5%;
  transition:all 100ms linear;
}

#add:hover{
  background:black;
  color:yellow;
  border:.3vmin solid yellow;
}


.list{
  width:90%;
  height:75%;
  position:absolute;
  border-radius:3vmin;
  top:24%;
  background:rgb(53, 50, 50);
  padding:3vmin;
  display:flex;
  flex-direction:row;
  flex-wrap:wrap;
  justify-content:space-around;
  gap:1vmin;
  overflow-x:hidden;
  overflow-y:scroll;
  -webkit-appearance: none;
  list-style-type: none;
}

.list::-webkit-scrollbar{
  background:black;
  width:1vmin;
}

.list::-webkit-scrollbar-thumb{
  background:yellow;
  border-radius: 3vmin;
  box-shadow: 0vmin 0vmin .1vmin black inset;
}


.list>li > h2{
  text-align: center;
  text-decoration: underline;
}


.list>li{
  width:30%;
  height:40%;
  color:black;
  background:yellow;
  padding:3vmin;
  overflow-x:hidden;
  overflow-y:scroll ;
  text-transform: capitalize;
}

.list > li::-webkit-scrollbar{
  background:black;
  width:1vmin;
}
.list  > li > p{
  font-weight: bold;
  font-size:3vmin;;
}

.list  > li::-webkit-scrollbar-thumb{
  background:yellow;
  border-radius: 3vmin;
  box-shadow: 0vmin 0vmin .1vmin black inset;
}
@media only screen and (max-width:550px){
.list>li > h2{
  text-align: center;
}


.list>li{
  width:100%;
  height:40%;
  color:black;
  background:yellow;
  padding:3vmin;
}
}